import React, {useState} from 'react';
import './Content.css';
import { Typography, Button,Box } from '@mui/material';

import Carbon from "../images/carbon.png"
import AppiceMain from "../images/appice-main.jpg"
import AppiceIntro from "../images/appice-intro.jpg"
import AppiceFeel from "../images/appice-feel.jpg"
import Tremvip from "../images/tremvip.png"


const Content = ( props: any) => {
    const [moreInfo,setMoreInfo] = useState(false)
    let type
    let title
    let overlayText

    switch(props.value){
        case "carbon":
             type = "web"
             title = "Carbon Flore"
             overlayText =  "Um website para a empresa Carbon Flore, que auxilia empresas a serem neutras em carbono. Atualmente feito em React, em breve receberá uma atualização em Next.js e autenticação com Firebase."
        break;

        case "appice":
            type = "mobile"
            title = "Appice"
            overlayText = "Aplicativo para ios e android feito com Expo e node. Em breve estará disponível."
       break;

       case "tremvip":
        type = "web"
        title = "Trem Vip"
        overlayText = "  Site de venda de ingressos feito em Next.js utilizando Vendure. Cada compra gera um QR Code criptografado que pode ser lido em um aplicativo complementar. Em breve no ar. "
        break;
    }

    const getImage = () => {

        switch(props.value){
            case "carbon":
                return (
                    <div                        
                    onMouseEnter={e => {setMoreInfo(true)}}
                    onMouseLeave={e => {setMoreInfo(false)}} 
                    style={moreInfo? { opacity:0.1, backgroundColor:'transparent'}:{ opacity:1,backgroundColor:'transparent'}}>
                        <img 
                            src={Carbon} 
                            className="image"
                            alt = ""
                        />
                    </div>
                )
    
            case "appice":
                return(
                    <div      
                        className={"ContainerImage"}                  
                        onMouseEnter={e => {setMoreInfo(true)}}
                        onMouseLeave={e => {setMoreInfo(false)}} 
                        style={moreInfo? { opacity:0.1, width:'90%'}:{ opacity:1, width:'90%'}}
                    >

                        <img src={AppiceIntro} alt="" className="appiceImg  "/>
                        <img src={AppiceMain} alt="" className="appiceImg  "/>
                        <img src={AppiceFeel} alt="" className="appiceImg  "/>
                    </div>
                    )
    
           case "tremvip":
                return(
                    <>
                        <div></div>
                        
                        <div                        
                            onMouseEnter={e => {setMoreInfo(true)}}
                            onMouseLeave={e => {setMoreInfo(false)}} 
                            style={moreInfo? { opacity:0.1, backgroundColor:'transparent'}:{ opacity:1,backgroundColor:'transparent'}}>

                            <img src={Tremvip} alt=""  className="image"/>
                        </div>
                        
                        <div ></div>
                    </>
                    )
        }
    }

    const getButton = () => {

        switch(props.value){
            case "carbon":
                return (
                    <a href="http://www.carbonflore.com.br" target="_blank" >   
                        <button className='button' >
                            <Typography variant="h6" sx={{backgroundColor:"transparent"}}>Visitar</Typography>
                        </button>
                    </a>
                    )

            case "appice":
                return (
                    <a >   
                        <button className='button' >
                            <Typography variant="h6" sx={{backgroundColor:"transparent"}}>Em breve</Typography>
                        </button>
                    </a>
                    )
    
           case "tremvip":
            return (
                <a >   
                    <button className='button' >
                        <Typography variant="h6" sx={{backgroundColor:"transparent"}}>Em breve</Typography>
                    </button>
                </a>
                )
        }

    }

    return (

        <div className={"Container"}>

            <div 
                className={"ContainerInfo"}>
                <Box >
                    <Typography variant="h6" sx={{backgroundColor:"#222222"}}>{type}</Typography>
                </Box>
                <Box >
                    <Typography variant="h4" sx={{backgroundColor:"#222222"}}>{title}</Typography>
                </Box>
            </div>

            <div  className={"ContainerImage"}>

                {getImage()}

                <div className="overlay"
                onMouseEnter={e => {setMoreInfo(true)}}
                onMouseLeave={e => {setMoreInfo(false)}}  
                >
                    <Typography variant="body1" sx={{backgroundColor:"transparent"}}>
                        {overlayText}
                    </Typography>

                    {getButton()}

                </div>

            </div>                

        </div>
    )
}

export default Content